	import tmSheet from '../../tm-vuetify/components/tm-sheet/tm-sheet.vue';
	
	export default {
		components:{
			tmLottie,
			tmAvatar,
			tmSheet
		},
		data() {
			return {
				value:'',
				text:[
					'淡淡一点的友情很真，淡淡一点的依恋很轻，淡淡一点的思念很深，淡淡一点的祝福最真！祝你新年快乐！',
					'不许动，举起手来！认识的站左边，不认识的站右边，想笑的站中间。说你呢！就是你！快放下手机，双手抱头靠墙站好，仔细给我听着：祝你新年快乐！',
					'新年里，愿你所有的故事都能精彩，所有的结局都能圆满，所有的梦想都能实现，所有的希望都能如愿，所有的好运都能预算，所有的付出都能兑现！新年快乐。',
					'朝朝暮暮快乐相守，互相扶持风雨同舟，年年岁岁有你作陪，浪漫情怀幸福陶醉，共饮新春爱情美酒，甜蜜味道绕心头，春节到了，亲爱的祝你新年快乐。',
					'新年只有一天，我们的友谊永存，敞开你的心扉，让我诚挚的友情，填满你美丽的心怀，祝你春节快乐。'
				],
