		methods: {
			send(){
				let san = require('../../static/san.json');
				console.log(san);
				console.log(this.$refs)
				this.$refs.lottie.registerAnimation(san);
			},
			getValue(){
				let index ='';
				if(this.index!=null&&this.index!=""){
					index = this.index;
				}else{
					index = Math.floor(Math.random()*5);
				}
				let text = "幻彩希："+this.text[index];
				let length = text.length;
				let count = 0;
				let time = setInterval(()=>{
					if(count==length){
						this.value = this.value.replace(/\|/g,"")
						clearInterval(time)
						return
					}
					this.value = this.value.replace(/\|/g,"")
					this.value += text[count++]+"|";
				},180)
				
			}
		}
